<template>
  <div id="app">
    <router-view />
    <Tabbar />
    <Looding/>
  </div>
</template>

<script>
  import Tabbar from './views/Tabbar.vue'
  import Looding from './views/Looding.vue'
  export default {
    components: { Tabbar,Looding },
    name: 'App',
    created() {
      this.$router.push('/')
    },
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333;
    font-size: 13.33333vw;
  }

  html,
  body,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  figure {
    margin: 0;
    padding: 0;
  }

  ul,
  li {
    list-style: none;
    padding: 0;
    margin: 0;
  }
</style>


