<template>
    <van-tabbar fixed v-model="active" active-color='#E90572'>
        <van-tabbar-item to="/">
            <span>首页</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.active : icon.normal">
        </van-tabbar-item>
        <van-tabbar-item to="/shop">
            <span>购物车</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.shop_active : icon.shop_normal">
        </van-tabbar-item>
        <van-tabbar-item to="/order">
            <span>我的订单</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.order_active : icon.order_normal">
        </van-tabbar-item>
        <van-tabbar-item to="/contact">
            <span>联系我们</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.contact_active : icon.contact_normal">
        </van-tabbar-item>
        <van-tabbar-item to="/complaints">
            <span>投诉建议</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.comp_active : icon.comp_normal">
        </van-tabbar-item>
        <van-tabbar-item to="/message">
            <span>消息</span>
            <img slot="icon" slot-scope="props" :src="props.active ? icon.msg_active : icon.msg_normal">
        </van-tabbar-item>
    </van-tabbar>
</template>

<script>
    
    export default {
        data() {
            return {
                active: 0,
                icon: {
                    normal: require("../assets/tabbar/t1-h.png"),
                    active: require('../assets/tabbar/t1.png'),
                    shop_normal: require('../assets/tabbar/t2-h.png'),
                    shop_active: require('../assets/tabbar/t2.png'),
                    order_normal: require('../assets/tabbar/t3-h.png'),
                    order_active: require('../assets/tabbar/t3.png'),
                    contact_normal: require('../assets/tabbar/t4-h.png'),
                    contact_active: require('../assets/tabbar/t4.png'),
                    comp_normal: require('../assets/tabbar/t5-h.png'),
                    comp_active: require('../assets/tabbar/t5.png'),
                    msg_normal: require('../assets/tabbar/t6-h.png'),
                    msg_active: require('../assets/tabbar/t6.png'),
                }
            }
        }
    }
</script>
<style scoped>
    .van-tabbar {
        background-color: #323232;
    }
</style>


