import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    is_looding:false
  },
  mutations: {
    show_looding(state){
      state.is_looding=true
    },
    hide_looding(state){
      state.is_looding=false
    }
  },
  actions: {

  }
})



