import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home.vue'
import About from '../views/home/About.vue'
Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      component: Home,
      redirect: 'about',
      children: [{
        path: 'about',
        component: About
      }, {
        path: 'answers',
        component: () => import('../views/home/Answers.vue')
      }, {
        path: 'panorama',
        component: () => import('../views/home/Panorama.vue')
      }, {
        path: 'room',
        component: () => import('../views/home/Room-reservations.vue')
      }, {
        path: 'design',
        component: () => import('../views/home/Design.vue')
      }, {
        path: 'roadwork',
        component: () => import('../views/home/Road-work.vue')
      }, {
        path: 'setmeal',
        component: () => import('../views/home/Set-meal.vue')
      }, {
        path: 'shopping',
        component: () => import('../views/home/Shopping-mall.vue')
      }, {
        path: 'gohome',
        component: () => import('../views/home/Go-home.vue')
      }]
    }, {
      path: '/shop',
      component: () => import('../components/Shop.vue')
    }, {
      path: '/order',
      component: () => import('../components/ordet.vue')
    }, {
      path: '/contact',
      component: () => import('../components/Contact.vue')
    }, {
      path: '/complaints',
      component: () => import('../components/Complaints.vue')
    }, {
      path: '/message',
      component: () => import('../components/Message.vue')
    }
  ]
})



